import Updux from 'updux';
import  { action, payload } from 'ts-action';
import u from 'updeep';
import { createSelector } from 'reselect';

import ftl from './ftl';
import engine, { calc_drive_rate } from './engine';
import { calc_ftl_rate } from './ftl/rules';

const set_ship_mass = action('set_ship_mass', payload());
const set_name = action('set_name',payload());

const dux = new Updux({
    subduxes: { ftl, engine },
    initial: {
        ship_type: {
            ship_class: null,
            name: '',
            mass: 0,
            cost: 0,
        },
        structure: {
            mass: 0,
            cost: 0,
            hull: 0
        }
    }
});

dux.addMutation( set_ship_mass, mass => u.updateIn('ship_type',{mass}));
dux.addMutation( set_name, name => u.updateIn('ship_type',{name}));

dux.addSubscription(
    store => createSelector(
        [ ship => ship.ship_type.mass, ship => ship.ftl.type ],
        (ship_mass,type) => store.dispatch(
            ftl.actions.set_ftl_rate(calc_ftl_rate(ship_mass,type)
        )        
    ))
);

dux.addSubscription(
    store => createSelector(
        [ ship => ship.ship_type.mass, ship => ship.engine ],
        (ship_mass,engine) => store.dispatch(
            dux.actions.set_drive_rate(calc_drive_rate(ship_mass,engine.rating))
        )        
    )
);


export default dux.asDux;

export const actions = dux.actions;
