import Updux from 'updux';
import  { action, payload } from 'ts-action';
import u from 'updeep';
import { createSelector } from 'reselect';

const set_drive_rating = action('set_drive_rating',payload());
const set_drive_rate = action('set_drive_rate',payload());

const dux = new Updux({
    initial: {
                mass: 0,
                cost: 0,
                rating: 0,
    }
});

dux.addMutation(set_drive_rating, rating => u({rating}));
dux.addMutation(set_drive_rate, rate => u(rate));

export function calc_drive_rate(ship_mass,rating,advanced=false) {
    const mass = Math.ceil(rating * 0.05 * ship_mass);
    const cost = mass * ( advanced ? 3 : 2 );

    return { mass, cost };
}

export default dux.asDux;
